<template>
	<view class="box">
		<!-- <view class="title">
			<image src="@/static/img/icon_read@3x.png" mode=""></image>
			<text>全部已读</text>
		</view> -->
		<scroll-view scroll-y class="message" @scrolltolower="handleLower">
			<message-list v-for="(item,index) in messageList" :key="index" :order="item"></message-list>
			<view v-if="messageList.length === 0" class="nono-list">
				————&nbsp&nbsp&nbsp&nbsp暂无数据&nbsp&nbsp&nbsp&nbsp————
				<!-- {{lower?'————&nbsp&nbsp&nbsp&nbsp暂无数据&nbsp&nbsp&nbsp&nbsp————':`————&nbsp&nbsp&nbsp&nbsp已加载完成&nbsp&nbsp&nbsp&nbsp————`}} -->
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import MessageList from './components/message-list/index.vue'
	import {
		PostMessageListAjax
	} from '@/apis/api/modules/user-center'
	export default {
		components: {
			MessageList
		},
		data() {
			return {
				pageIndex: 1,
				messageList: [],
				lower: true
			}
		},
		onLoad() {
			this.getData()
		},
		methods: {
			async getData() {
				const result = await PostMessageListAjax({
					PageIndex: this.pageIndex,
					PageSize: 10,
					token: true
				})
				if (result.Result.List.length === 0) {
					this.lower = true
					return false
				}
				// this.lower = false
				this.messageList = result.Result.List
			},
			async handleLower() {
				// if (!this.lower) {
				// 	return false
				// }
				this.pageIndex = this.pageIndex + 1

				const result = await PostMessageListAjax({
					PageIndex: this.pageIndex,
					PageSize: 10,
					token: true
				})
				if (result.Result.List.length > 0) {
					this.lower = true
					this.messageList.push(...result.Result.List)
					return false
				}

				this.lower = false
			},
		}
	}
</script>

<style scoped lang="scss">
	.box {
		height: 100%;
		background: #F7F7F7;
		padding: 30rpx;
		display: flex;
		flex-direction: column;
		box-sizing: border-box;
	}

	.title {
		width: 100%;
		display: flex;
		justify-content: end;
		align-items: center;
		font-size: 28rpx;
		font-weight: 400;
		color: #999999;
		line-height: 40rpx;
		height: 40rpx;
		text-align: right;

		image {
			width: 40rpx;
			height: 40rpx;
		}
	}

	.message {
		flex: 1;
		overflow: auto;
	}

	.nono-list {
		text-align: center;
		color: #c1c1ba;
		padding: 30rpx 0;
	}
</style>
